.descriptionBlock{
    display: flex;
    padding: 10px;
    font-size: 30px
}

.descriptionBlock img{
    margin-right: 10px
}

.mainPhoto{
    max-width: 200px;
}

.touchBlock{
    position: relative;
    cursor: pointer;
}

.spanBlock{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    color: #B22222;
    padding: 0 15px 0 14.6px;
    transition: .5s;
    font-size: 19px;
}

.spanBlock:hover{
    color: #000;
    background-color:wheat;
}

.touchBlock:hover .spanBlock{
    display: block;
}

.infoBlock b{
    font-size: 22px;
}

.toggleEditMode{
    display: none;
}

.infoBlock:hover .toggleEditMode{
    display: block;
}

.form{
    margin: 10px 0 0 10px;
}

.input{
    margin-top: 5px;
}

.flexBox{
    display: flex;
}

.formBlock{
    margin-top: 10px;
}

.textarea{
    max-height: 34px;
}

.contact{
    margin-top: 10px;
    margin-left: 10px;
}


.formSummaryError{
    border: 2px solid #8B0000;
    padding: 5px;
    color: #B22222;
    background-color: #F08080;
    width: 160px;
    margin-bottom: 10px;
    transition: 1s;
}

.formSummaryError:hover{
    background-color: #CD5C5C;
    color: #FFA07A;
}

.myContacts{
    margin-left: 10px;
}